const TabsFunc = (tabNav, tabNavActive, tabContent, tabContentActive) => {
  tabNav = document.querySelectorAll(tabNav);
  tabContent = document.querySelectorAll(tabContent);
  let tabs,
      tabName;

  tabNav.forEach((item) => {
    item.addEventListener('click', selectTabNav)
  });

  function selectTabNav() {
    tabNav.forEach((item) => {
      item.classList.remove(tabNavActive);
    });
    this.classList.add(tabNavActive); 
    tabName = this.getAttribute('data-tab');
    selectTabContent(tabName);
  }

  function selectTabContent(tab) {
    tabContent.forEach((item) => {
      tabs = item.getAttribute('data-tab');
      if (tabs === tab) {
        item.classList.add(tabContentActive);
      } else {
        item.classList.remove(tabContentActive);
      }
    });
  }
}

const SelectTab = (obj) => {
  let tab = obj.options[obj.selectedIndex].getAttribute('data-tab');
  
  const selectTabContentOpt = () => {
    const tabContent = document.querySelectorAll('.property-filters-tab-content');
    tabContent.forEach((item) => {
      let tabs = item.getAttribute('data-tab');
      if (tabs === tab) {
        item.classList.add('active-flex');
      } else {
        item.classList.remove('active-flex');
      }
    });
  }

  selectTabContentOpt();
}

TabsFunc('.property-filters__main-btn', 'active-btn');
TabsFunc('.property-cards-filter__btn', 'active-btn');
TabsFunc('.personal-area-tab-list__btn', 'active-btn-br', '.personal-area-tab-content', 'active-flex');